import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Box } from "@material-ui/core";
import SnippetCard from "@components/snippet-card";
import NoView from "@components/no-view";

const useStyles = makeStyles((theme) => {
  return {
    root: {},
  };
});

export default function Snippets({ snippets }) {
  const classes = useStyles();
  return (
    <Box className={classes.root}>
      {snippets.length > 0 ? (
        <Box className={classes.snippetsArea}>
          {snippets.map((snippet, index) => {
            return (
              <Box
                key={snippet.snippetId + "Ad" + index}
                style={{ display: "inline-block" }}
              >
                <SnippetCard {...snippet} />
              </Box>
            );
          })}
        </Box>
      ) : (
        <NoView
          primaryText="No snippets"
          secondaryText="You haven't created any snippets yet."
          description="Choose a programming language and create snippets. They will start appearing here. All your snippets are stored in your github account"
        />
      )}
    </Box>
  );
}
