import axios from "axios";
import Cookies from "js-cookie";

import { useAsync } from "@utils/hooks";
import { BASE_URL } from "@constants/url";

export const useGetDevelopers = () => {
  const { exec, inProgress, error, status, data } = useAsync(
    async ({ developersUrl, page }) =>
      axios.get(BASE_URL + "/developers", {
        params: {
          accessToken: Cookies.get("access_token"),
          developersUrl,
          page,
        },
        headers: {
          Accept: "application/json",
        },
      })
  );

  return {
    getDevelopersTrigger: exec,
    isGetDevelopersInProgress: inProgress,
    getDevelopersStatus: status,
    getDevelopersData: data,
    getDevelopersError: error,
  };
};
